import React from 'react'

interface InputProps {
    label: string
    type?: string
    value: string
    onChange: (value: string) => void
    placeholder?: string
    required?: boolean
}

export function Input({
    label,
    type = 'text',
    value,
    onChange,
    placeholder,
    required
}: InputProps) {
    return (
        <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label}
            </label>
            <input
                type={type}
                value={value}
                onChange={(e) => onChange(e.target.value)}
                placeholder={placeholder}
                required={required}
                className="w-full border border-gray-300 rounded px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
        </div>
    )
}

interface TextAreaProps {
    label: string
    value: string
    onChange: (value: string) => void
    rows?: number
    placeholder?: string
}

export function TextArea({
    label,
    value,
    onChange,
    rows = 4,
    placeholder
}: TextAreaProps) {
    return (
        <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label}
            </label>
            <textarea
                value={value}
                onChange={(e) => onChange(e.target.value)}
                rows={rows}
                placeholder={placeholder}
                className="w-full border border-gray-300 rounded px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
        </div>
    )
}

interface ArrayFieldProps {
    label: string
    value: string[]
    onChange: (value: string) => void
    placeholder?: string
}

export function ArrayField({
    label,
    value,
    onChange,
    placeholder
}: ArrayFieldProps) {
    return (
        <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label} (one per line)
            </label>
            <textarea
                value={value.join('\n')}
                onChange={(e) => onChange(e.target.value)}
                rows={4}
                placeholder={placeholder}
                className="w-full border border-gray-300 rounded px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
        </div>
    )
}

interface ImageUploadProps {
    label: string
    currentImage?: string
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void
}

export function ImageUpload({
    label,
    currentImage,
    onChange
}: ImageUploadProps) {
    return (
        <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label}
            </label>
            <input
                type="file"
                accept="image/*"
                onChange={onChange}
                className="w-full border border-gray-300 rounded px-3 py-2"
            />
            {currentImage && (
                <div className="mt-2">
                    <img
                        src={currentImage}
                        alt="Current"
                        className="h-20 object-cover rounded border"
                    />
                </div>
            )}
        </div>
    )
}